<template>
  <div>
    <!--包导航区域-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>博客网页管理</el-breadcrumb-item>
      <el-breadcrumb-item>管理信息</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 设备主体内容-->
    <el-card class="box-card">
      <el-row :gutter="20" type="flex" justify="space-between">
        <el-col :span="8">
         <el-input v-model="searchInfo" placeholder="请输入内容" clearable>
          <el-button @click="search()" slot="append" icon="el-icon-search"></el-button>
         </el-input>
        </el-col>
        <el-col :span="4"><el-button type="primary" @click="addDialogVisible = true">添加资源</el-button>
        </el-col>
      </el-row>

    <!--设备信息表格-->
      <el-table :data="info" border stripe>
        <el-table-column label="#" type="index" width="20px"></el-table-column>
        <el-table-column label="链接地址" prop="url"></el-table-column>
        <el-table-column label="资源类型" prop="type"></el-table-column>
        <el-table-column label="标题" prop="title"></el-table-column>
        <el-table-column label="创建时间" prop="ctime"></el-table-column>
        <el-table-column label="备注" prop="demo"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="编辑设备" placement="top-start" :enterable="false">
            <el-button size="mini" type="success" @click="showEdit(scope.row)">编辑</el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="编辑设备" placement="top-start" :enterable="false">
              <el-button size="mini" type="danger" @click="showDelete(scope.row.url)">删除</el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--添加对话框-->
    <el-dialog title="添加资源" :visible.sync="addDialogVisible" width="50%">
      <!--      内容主题区域-->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px" class="login_form">
        <el-form-item label="用户名称" prop="username">
          <el-input v-model="addForm.username" placeholder="请输入用户名" disabled></el-input>
        </el-form-item>
        <el-form-item label="链接地址" prop="url">
          <el-input v-model="addForm.url" placeholder="请输入链接地址"></el-input>
        </el-form-item>
        <el-form-item label="资源类型" prop="type">
          <el-select v-model="addForm.type" placeholder="请选择资源类型">
            <el-option label="网站" value="网站"></el-option>
            <el-option label="博客" value="博客"></el-option>
            <el-option label="视频" value="视频"></el-option>
            <el-option label="其他" value="其他"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="资源标题" prop="title">
          <el-input v-model="addForm.title" placeholder="请输入资源标题"></el-input>
        </el-form-item>
        <el-form-item label="资源备注" prop="demo">
          <el-input v-model="addForm.demo" placeholder="请输入资源备注"></el-input>
        </el-form-item>
      </el-form>
      <!--      底部区域-->
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addSubmit()">确 定</el-button>
      </div>
    </el-dialog>

    <!--编辑对话框-->
    <el-dialog title="编辑资源" :visible.sync="editDialogVisible" width="50%">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="80px" class="login_form">
        <el-form-item label="用户名称" prop="username">
          <el-input v-model="editForm.username" placeholder="请输入用户名" disabled></el-input>
        </el-form-item>
        <el-form-item label="链接地址" prop="url">
          <el-input v-model="editForm.url" placeholder="请输入链接地址" disabled></el-input>
        </el-form-item>
        <el-form-item label="资源类型" prop="type">
          <el-select v-model="editForm.type" placeholder="请选择资源类型">
            <el-option label="网站" value="网站"></el-option>
            <el-option label="博客" value="博客"></el-option>
            <el-option label="视频" value="视频"></el-option>
            <el-option label="其他" value="其他"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="资源标题" prop="title">
          <el-input v-model="editForm.title" placeholder="请输入资源标题"></el-input>
        </el-form-item>
        <el-form-item label="资源备注" prop="demo">
          <el-input v-model="editForm.demo" placeholder="请输入资源备注"></el-input>
        </el-form-item>
      </el-form>
      <!--底部区域-->
      <div slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false" >取 消</el-button>
        <el-button type="primary" @click="editSubmit()" >修 改</el-button>
      </div>
    </el-dialog>

    <!--删除对话框-->
    <el-dialog title="删除资源" :visible.sync="delteDialogVisible" width="40%">
      <p>是 否 确 定 要 永 久 删 除 这 条 资 源？</p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="delteDialogVisible = false" >取 消</el-button>
        <el-button type="primary" @click="deleteSubmit()" >确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      info: [],
      total: 0,
      searchInfo: '',
      // 添加、编辑、删除对话框显示
      addDialogVisible: false,
      editDialogVisible: false,
      delteDialogVisible: false,
      addForm: {
        username: window.sessionStorage.getItem('username'),
        url: 'https://',
        type: '',
        demo: '无',
        title: ''
      },
      addFormRules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        url: [{ required: true, message: '请输入链接地址', trigger: 'blur' }],
        type: [{ required: true, message: '请选择资源类型', trigger: 'blur' }],
        title: [{ required: true, message: '请选择资源标题', trigger: 'blur' }],
        demo: [{ required: true, message: '请选择资源备注', trigger: 'blur' }]
      },
      editForm: {
        username: '',
        url: '',
        type: '',
        demo: '无',
        title: ''
      },
      editFormRules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        url: [{ required: true, message: '请输入链接地址', trigger: 'blur' }],
        type: [{ required: true, message: '请选择资源类型', trigger: 'blur' }],
        title: [{ required: true, message: '请选择资源标题', trigger: 'blur' }],
        demo: [{ required: true, message: '请选择资源备注', trigger: 'blur' }]
      },
      deleteForm: {
        username: window.sessionStorage.getItem('username'),
        url: ''
      }
    }
  },
  created () {
    this.getDeviceList()
  },
  methods: {
    async getDeviceList () {
      const { data: res } = await this.$axios.get('http://localhost:3000/users/info', { params: { username: window.sessionStorage.getItem('username'), token: window.sessionStorage.getItem('token') } })
      if (res.code !== 200) return false
      else {
        this.info = res.data
      }
    },
    async search () {
      // 搜索函数
    },
    addSubmit () {
      // 添加信息函数
      this.addDialogVisible = false
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$axios.get ('http://localhost:3000/users/insert',
          { params: { username: this.addForm.username, url: this.addForm.url, type: this.addForm.type, title: this.addForm.title, demo: this.addForm.demo, token: window.sessionStorage.getItem('token') } })
        // console.log(res)
        if (res.code !== 200) this.$message.error('添加信息错误')
        else {
          this.$message.success('添加成功')
        }
      })
    },
    showEdit (arr) {
      // 显示编辑对话框
      this.editDialogVisible = true
      this.editForm.username = arr.username
      this.editForm.url = arr.url
      this.editForm.type = arr.type
      this.editForm.title = arr.title
      this.editForm.demo = arr.demo
    },
    editSubmit () {
      // 编辑修改数据库提交
      this.editDialogVisible = false
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$axios.get ('http://localhost:3000/users/edit',
          { params: { type: this.editForm.type, title: this.editForm.title, demo: this.editForm.demo, username: this.editForm.username, url: this.editForm.url, token: window.sessionStorage.getItem('token') } })
        // console.log(res)
        if (res.code !== 200) this.$message.error('修改信息错误')
        else {
          this.$message.success('修改成功')
        }
      })
    },
    showDelete (url) {
      // 删除数据函数
      this.delteDialogVisible = true
      this.deleteForm.url = url
    },
    async deleteSubmit () {
      const { data: res } = await this.$axios.get('http://localhost:3000/users/delete', { params: { username: this.deleteForm.username, url: this.deleteForm.url, token: window.sessionStorage.getItem('token') } })
      if (res.code !== 200) return this.$message.error('删除信息失败')
      else {
        return this.$message.success('删除成功')
      }
    }
  }
}
</script>

<style scoped>
.el-breadcrumb {
  margin-bottom: 15px;
  /*font-size: 12px;*/
}
.el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
}
.el-table {
  margin-top: 15px;
}
</style>
