<template>
  <div id="login">
    <div class="login_box">
        <div class="avatar_box">
          <!--头像区域-->
          <img src="../assets/logo.png" alt=""/>
        </div>

        <div>
        <!--表单区域-->
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="login_form">
            <el-form-item  prop="username">
              <el-input prefix-icon="el-icon-s-custom" v-model="ruleForm.username" placeholder="帐号"></el-input>
            </el-form-item>
            <el-form-item prop="password" >
              <el-input type="password" v-model="ruleForm.password" prefix-icon="el-icon-s-cooperation"  placeholder="密码"></el-input>
            </el-form-item>
            <el-form-item class="login_button">
              <el-button type="success" @click="submitForm()">登录</el-button>
              <el-button type="info" @click="resetForm()">重置</el-button>
            </el-form-item>
          </el-form>

        </div>

    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      ruleForm: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入帐号', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm () { // 手机验证码登录
      this.$refs.ruleForm.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$axios.get ('http://localhost:3000/login', { params: { name: this.ruleForm.username, password: this.ruleForm.password } })
        // console.log(res.token)
        if (res.code !== 200) this.$message.error('账号或验证码错误')
        else {
          this.$message.success('登录成功')
          window.sessionStorage.setItem('username', this.ruleForm.username)
          window.sessionStorage.setItem('token', res.token)
          this.$router.push('/home')
        }
      })
    },
    resetForm () {
      // 重置
      this.$refs.ruleForm.resetFields()
    }
  }
}
</script>

<style scoped>
  #login {
    background-color: #2b4b6b;
    height: 100%;
  }
  .login_box {
    height: 300px;
    width: 450px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .avatar_box{
    background-color: #fff;
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    position: absolute;
    left: 50%;
    box-shadow: 0 0 10px #ddd;
    transform: translate(-50%, -50%);
  }
  .avatar_box img {
    height: 100%;
    width: 100%;
    border-radius: 50%;
    background-color: #eeee;
  }
  .login_form {
    position: absolute;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    bottom: 0;
  }
  .login_button {
    display: flex;
    justify-content: flex-end;
  }
</style>
